import focusNav from '../../components/focusNav.vue'
import tab from '../../components/tab.vue'
import focusList from '../../components/focusList.vue'

export default {
	components:{
		focusNav,
		tab,
		focusList
	},
	data() {
		return {
			list: [],
			page: "friend"
		}
	},
	methods: {
		getVideos() {
			uni.request({
				url: "http://localhost:8088/json/videos.json",
				success: (res) => {
					this.list = res.data.list.slice(4, 11);
				}
			})
		}
	},
	created() {
		this.getVideos();
	}
}
